import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api";

class StageService extends BaseApiService {
  // ------------------------- To get stages  ------------------------- //

  getStages = (start = 0, limit = 10, search: string) => {
    const endPoint =
      DEFAULT_PATH + `/stage/initiation/detail/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
  getReapealInitiationStage = (search: string) => {
    const endPoint =
      DEFAULT_PATH +
      `/stage/initiation/detail/search?stage.stageName:in=[CREDIT_AND_QUERY,LOGIN]&isActive=true${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------- To get stage history ------------------------- //

  getStageHistory = (start = 0, limit = 10, search: string) => {
    const endPoint = DEFAULT_PATH + `/tat/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
  getReappealDetails = (uid: any) => {
    const endPoint = DEFAULT_PATH + `/stage/manage/details/${uid}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------- To find the external for stage ------------------------- //
  findExternalVendor = (start = 0, limit = 10, search = "") => {
    const endPoint =
      DEFAULT_PATH +
      `/partner/search?isActive=true&name:lk=${search}&start=${start}&limit=${limit}`;

    return this.makeGetRequestWithAuth(endPoint);
  };
  getExternalVendor = (start = 0, limit = 10, search = "") => {
    const endPoint =
      DEFAULT_PATH + `/partner/manage/stage/filter?start=${start}&limit=${limit}${search}`;

    return this.makeGetRequestWithAuth(endPoint);
  };
  getInternalVendor = (start = 0, limit = 10, search = "") => {
    const endPoint =
      DEFAULT_PATH +
      `/user/profile/manage/filter/internal/vendor?start=${start}&limit=${limit}${search}`;

    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------- To find the assignee / delegate for stage ------------------------- //
  findStageAssignee = (start = 0, limit = 10, search = "", loanId: string, stage: string) => {
    const endPoint =
      DEFAULT_PATH +
      `/user/profile/manage/filter/assignee?loanUid=${loanId}&stageName=${stage}&name=${search}&start=${start}&limit=${limit}`;

    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------- To initiate stage ------------------------- //

  initiateStage = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/stage/manage`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };
  initiateStageReapeal = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/stage/manage`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // -------------------------To reject application ----------------------- //

  rejectLoan = (loanId: string, payload: any) => {
    const endPoint = DEFAULT_PATH + `/stage/manage/reject/loan/${loanId}`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };
  acceptOrRejectReappealLoan = (uid: any, payload: any) => {
    const endPoint = DEFAULT_PATH + `/stage/manage`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------- for actions delegate/re-appeal/re-open ------------------------//

  updateStage = (payload: any) => {
    const url = `${DEFAULT_PATH}/stage/manage`;
    return this.makePutRequestWithAuth(url, payload);
  };

  createReAppeal = (payload: any) => {
    const url = `${DEFAULT_PATH}/stage/manage`;
    return this.makePostRequestWithAuth(url, payload);
  };

  // ------------------------- To get the collateral property details ---------------------- //
  getPropertyDetails = (start = 0, limit = 10, search = "") => {
    const endPoint =
      DEFAULT_PATH + `/property/address/search?${search}&start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ---------------------- To get the customer details --------------------------------- //
  getCustomerDetails = (start = 0, limit = 10, search = "") => {
    const endPoint = DEFAULT_PATH + `/applicant/search?${search}&start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ---------------------- To get  Stage details --------------------------------- //
  getStageInitiationDetails = (stageUid: string) => {
    const endPoint =
      DEFAULT_PATH + `/stage/initiation/detail/search?stage.uid=${stageUid}&isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To get all stage related details --------------------------- //
  getStageDetails = (stageUid: string) => {
    const endPoint = DEFAULT_PATH + `/stage/manage/details/${stageUid}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To get repayment history --------------------------- //
  getRepaymentHistory = (start = 0, limit = 10, search: string) => {
    const endPoint =
      DEFAULT_PATH +
      `/stage/credit/repayment/history/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To get stage Documents scrutinized --------------------------- //
  getDocumentScrutinized = (start = 0, limit = 10, search = "") => {
    const endPoint = DEFAULT_PATH + `/stage/document/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To get stage Deviation search --------------------------- //
  getLegalDeviations = (start = 0, limit = 10, search = "") => {
    const endPoint = DEFAULT_PATH + `/loan/deviation/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To get stage name --------------------------- //
  getStageName = (loanId: string) => {
    const endPoint = DEFAULT_PATH + `/stage/manage/names?loanUid=${loanId}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  stageSearch = (search: string) => {
    const endPoint = DEFAULT_PATH + `/stage/search?isActive=true${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new StageService();
